/** 公共样式 */
.clearfix:before,
.clearfix:after {
  display: table;
  content: " ";
}
.clearfix:after {
  clear: both;
}
.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}
.card {
  background-color: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(215, 215, 215, 0.5);
  border-radius: 10px;
}
.fixed {
  position: fixed;
}
a:hover {
  color: #ff3a2e;
}
/** 清理样式 */
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
html,
body {
  font-family: MicrosoftYaHei;
  font-weight: normal;
  font-stretch: normal;
  line-height: 30px;
  font-size: 16px;
  letter-spacing: 0px;
  color: #333;
}
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  /* Firefox */
  -webkit-box-sizing: border-box;
  /* Safari */
}
ul,
ul > li {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
/** 顶部 */
.top-header {
  height: 30px;
  background-color: #000000;
  color: #FFF;
  font-size: 14px;
}
.top-header > .left {
  width: 50%;
  padding-left: 10%;
  position: relative;
  display: flex;
}
.top-header > .right {
  width: 50%;
  text-align: right;
  padding-right: 10%;
}
.top-header > .right .txt,
.top-header > .left .txt {
  font-size: 0.7vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 30px;
  letter-spacing: 0px;
  position: relative;
  color: #FFF;
  display: inline;
  cursor: pointer;
}
.top-header > .left .line {
  margin: 9px 8px;
  height: 12px;
  width: 1px;
  background: #FFF;
}
/** 顶部导航 */
.top-bar {
  height: 5.2vw;
  padding-left: 50%;
  background-color: #FFF;
  background-image: url(./images/logo_with_text.png);
  background-repeat: no-repeat;
  background-size: 11.1vw 2.4vw;
  background-position: 20% center;
  margin: 0;
}
.top-bar > li {
  line-height: 5.2vw;
  display: inline-block;
}
.top-bar > li > a {
  font-size: 0.9vw;
  color: #333333;
}
.top-bar > li > a:hover,
.top-bar > li > a.active {
  color: #ff3a2e;
}
.top-bar > li + li {
  margin-left: 38px;
}
