@import '~swiper/dist/css/swiper.css';
/** 首页 轮播图 */
.first-page-container .swiper-container {
  width: 100%;
  height: 42vw;
}
.first-page-container .swiper-wrapper .swiper-slide {
  background-repeat: no-repeat;
  background-size: 100%;
}
.first-page-container .swiper-wrapper .swiper-slide.banner1 {
  background-image: url(~images/banner_1.png);
}
.first-page-container .swiper-wrapper .swiper-slide.banner2 {
  background-image: url(~images/banner_2.png);
}
.first-page-container .swiper-wrapper .swiper-slide.banner3 {
  background-image: url(~images/banner_3.png);
}
/** 首页 统计数据*/
.first-page-container .static-data-container {
  width: 50%;
  margin: 0 auto;
  margin-top: 24px;
}
.first-page-container .static-data-container .item {
  display: inline-block;
  width: 20%;
}
.first-page-container .static-data-container .item + .item {
  margin-left: 20%;
}
.first-page-container .static-data-container .item .data {
  font-size: 2.5vw;
  line-height: 2.5vw;
  color: #ff3a2e;
  text-align: center;
  white-space: nowrap;
}
.first-page-container .static-data-container .item .title {
  margin-top: 1vw;
  font-size: 1.3vw;
  line-height: 1.6vw;
  text-align: center;
  white-space: nowrap;
}
/** 产品介绍 */
.production-info {
  margin: 100px auto;
  padding: 100px 0;
  width: 65%;
}
.production-info h3,
.in-flow h3,
.collaborator h3,
.about-us h3 {
  font-size: 1.6vw;
  line-height: 19.2px;
  letter-spacing: 1.8px;
  text-align: center;
  margin: 0 0 20px 0;
  position: relative;
}
.production-info h3::after,
.in-flow h3::after,
.collaborator h3::after,
.about-us h3::after {
  content: ' ';
  position: absolute;
  bottom: -20px;
  left: 50%;
  left: calc(50% - 25px);
  width: 50px;
  height: 2px;
  background-color: #ff3a2e;
  /** 后补 */
  background-image: linear-gradient(90deg, #ff3a2e 0%, #ff742e 100%);
}
.about-us h3 {
  color: #FFF;
}
.about-us h3::after {
  background-color: #FFF;
  /** 后补 */
  background-image: none;
}
.production-info .list {
  margin-top: 70px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
}
.production-info .list .item {
  height: 24.9vw;
  padding-top: 16vw;
  width: 14.5vw;
  background-repeat: no-repeat;
  background-position: center 4.5vw;
  background-size: 8.3vw auto;
}
.production-info .list .item.item1 {
  background-image: url(~images/production_1.png);
}
.production-info .list .item.item2 {
  background-image: url(~images/production_2.png);
}
.production-info .list .item.item3 {
  background-image: url(~images/production_3.png);
}
.production-info .list .item.item4 {
  background-image: url(~images/production_4.png);
}
.production-info .list .item {
  margin: 0 0.8vw;
}
.production-info .list .item .title {
  color: #333;
  font-size: 0.9vw;
  text-align: center;
}
.production-info .list .item .info,
.in-flow .list .item .info {
  color: #666;
  font-size: 0.7vw;
  padding: 0 1vw 3.6vw 1vw;
  line-height: 1.3vw;
  text-align: center;
}
/** 入驻流程 */
.in-flow {
  z-index: -4;
  background-color: #f8f8f8;
  padding: 60px 20% 112px 20%;
  box-sizing: content-box;
  position: relative;
}
.in-flow::before {
  display: block;
  content: ' ';
  position: absolute;
  left: 22%;
  right: 22%;
  top: 10vw;
  top: calc(3vw + 99px);
  z-index: -1;
  width: 56%;
  height: 22.4vw;
  background-color: #ff3e34;
  background-image: linear-gradient(90deg, #ff3e34 0%, #ff4e45 100%);
  border-radius: 10px;
}
.in-flow::after {
  content: 'E-COMMERCE';
  position: absolute;
  left: 22%;
  right: 22%;
  bottom: 116px;
  z-index: -2;
  text-align: center;
  height: 5vw;
  font-family: Arial-BoldMT;
  font-size: 7.8vw;
  line-height: 36px;
  letter-spacing: -3.7px;
  color: #ededed;
}
.in-flow .list {
  padding-top: 4.7vw;
}
.in-flow .list .item {
  height: 14.1vw;
  padding-top: 7.6vw;
  width: 10.8vw;
  background-repeat: no-repeat;
  background-position: center 2.6vw;
  background-size: 3.6vw auto;
}
.in-flow .list .item.item1 {
  background-image: url(~images/in_1.png);
}
.in-flow .list .item.item2 {
  background-image: url(~images/in_2.png);
}
.in-flow .list .item.item3 {
  background-image: url(~images/in_3.png);
}
.in-flow .list .item.item4 {
  background-image: url(~images/in_4.png);
}
.in-flow .list .item.item5 {
  background-image: url(~images/in_5.png);
}
.in-flow .list .item + .item {
  margin-left: 1.25vw;
}
.in-flow .list .item .title {
  color: #333;
  font-size: 0.9vw;
  text-align: center;
}
.in-flow .list .item .info {
  text-align: center;
  color: #666;
  font-size: 0.8vw;
  padding: 0 20px 0px 20px;
}
.in-flow .btn-link {
  margin: 5vw auto;
  display: block;
  text-align: center;
  background-color: #ffffff;
  box-shadow: 0px 0px 10px 0px rgba(215, 215, 215, 0.5);
  border-radius: 30px;
  width: 13vw;
  height: 3.1vw;
  font-size: 1.3vw;
  letter-spacing: 1.4px;
  color: #ff3a2e;
  line-height: 3.1vw;
  cursor: pointer;
}
/** 合作商家 */
.collaborator {
  margin: 0 12%;
  padding: 100px 0;
}
.collaborator .swiper-container {
  width: 76vw;
  height: 37.3vw;
  margin-top: 20px;
  background-repeat: no-repeat;
  background-size: 71.3vw auto;
  background-image: url(~images/collaborator_back_map.png);
  background-position: center;
}
.collaborator .swiper-container .swiper-wrapper .swiper-slide {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.collaborator .swiper-container .swiper-wrapper .swiper-slide .collaborator-list {
  width: 65vw;
  text-align: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.collaborator .swiper-container .swiper-wrapper .swiper-slide .collaborator-list > img {
  width: 15%;
  height: auto;
}
.collaborator .swiper-container .swiper-button-next,
.collaborator .swiper-container .swiper-button-prev {
  width: 3.6vw;
  height: 3.6vw;
  border-radius: 50%;
  background-color: #000000;
  opacity: 0.5;
  background-color: rgba(0, 0, 0, 0.5);
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center;
  top: calc(50% - 1.8vw + 20px);
}
.collaborator .swiper-container .swiper-button-next {
  background-image: url(~images/collaborator_next.png);
}
.collaborator .swiper-container .swiper-button-prev {
  background-image: url(~images/collaborator_prev.png);
}
/** 关于我们 */
.about-us {
  padding-top: 8vw;
  width: 100%;
  height: 55.4vw;
  background-repeat: no-repeat;
  background-size: 100%;
  background-image: url(~images/about_us_back.png);
}
.about-us .card {
  width: 31.3vw;
  background-color: #ffffff;
  border-radius: 0.5vw;
  margin: 5vw auto 0 auto;
  margin: calc(3vw + 39px) auto 0 auto;
  overflow: hidden;
}
.about-us .card .logo {
  width: 6.3vw;
  height: 6.3vw;
  border: solid 1px #333333;
  border-radius: 50%;
  background-image: url(~images/about_us_logo.png);
  background-repeat: no-repeat;
  background-size: 100%;
  margin: 2.6vw auto 0 auto;
}
.about-us .card .title {
  margin-top: 20px;
  font-size: 1.6vw;
  line-height: 30px;
  letter-spacing: 1.8px;
  color: #333333;
  text-align: center;
}
.about-us .card .info {
  text-align: center;
  font-size: 0.8vw;
  line-height: 30px;
  color: #333333;
  margin: 2.1vw 4.4vw 4.2vw 4.4vw;
}
/** 底部 */
.footer {
  background-color: #000;
  color: #FFF;
}
.footer .top {
  width: 62.5vw;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 30px;
  border-bottom: 1px solid #656973;
}
.footer .top .help-center {
  width: 30vw;
  padding-left: 4vw;
}
.footer .top .help-center h5 {
  font-size: 18px;
  margin: 0 0 1.5vw 0;
  color: #FFF;
}
.footer .top .help-center a {
  display: block;
  text-decoration: none;
  font-size: 14px;
  color: #ffffff;
  opacity: 0.5;
}
.footer .top .qr-code {
  width: 9vw;
  text-align: center;
}
.footer .top .qr-code img {
  width: 80px;
}
.footer .top .qr-code .title {
  font-size: 14px;
  color: #ffffff;
  opacity: 0.5;
}
.footer .top .contact {
  width: 14vw;
  text-align: center;
}
.footer .top .contact .icon {
  margin: 0 auto 1vw auto;
  width: 40px;
  height: 40px;
  border: solid 1px #ffffff;
  border-radius: 50%;
  background-image: url(~images/bottom_phone.png);
  background-repeat: no-repeat;
  background-size: 100%;
}
.footer .top .contact .info {
  font-size: 20px;
  color: #ffffff;
  opacity: 0.5;
}
.footer .top .contact .remark {
  font-size: 14px;
  color: #ffffff;
  opacity: 0.5;
}
.footer .bottom {
  padding-top: 20px;
  text-align: center;
  font-size: 14px;
}
.footer .bottom .copyright,
.footer .bottom .address {
  opacity: 0.5;
}
.footer .bottom .rela-info {
  width: 21.2vw;
  margin: 0 auto;
  padding: 1vw 0;
}
.footer .bottom .rela-info .item {
  display: inline-block;
  width: 4.3vw;
  height: 1.6vw;
  margin: 0 0.5vw;
  background-repeat: no-repeat;
  background-size: 100%;
}
.footer .bottom .rela-info .item.item1 {
  background-image: url(~images/bottom_a.png);
}
.footer .bottom .rela-info .item.item2 {
  background-image: url(~images/bottom_b.png);
}
.footer .bottom .rela-info .item.item3 {
  background-image: url(~images/bottom_c.png);
}
.footer .bottom .rela-info .item.item4 {
  background-image: url(~images/bottom_d.png);
}
#right-bar-container {
  z-index: 5;
}
#join-us {
  display: inline;
  visibility: hidden;
}
.top-fiexed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
  box-shadow: 0 1px 1px #f0f1f2;
}
.first-page-container {
  padding-top: calc(5.2vw + 30px);
}
.in-flow,
.collaborator,
.about-us,
.first-page-container,
.production-info {
  position: relative;
}
.with-anchor {
  position: absolute;
  top: -90px;
  top: calc(0px - 5.2vw - 30px);
}
