
/** fixed 框*/
.fixed-qr{
    z-index:5;
    right:1.6vw;
    width:5.2vw;
    bottom: 40px;
    background-color: #ffffff;
	box-shadow: 0px 0px 20px 0px 
		rgba(215, 215, 215, 0.5);
	border-radius: 5px;
}
.fixed-qr .qr-code-list{
    display: none;
}

.fixed-qr .qr-code{
    padding-top:1vw;
    text-align: center;
}
.fixed-qr .qr-code:nth-last-of-type(1){
    padding-bottom: 1vw;
}

.fixed-qr .qr-code img{
    width:4.2vw;
    display: block;
    margin:0 auto;
}
.fixed-qr .qr-code .title{
    margin-top:5px;
    font-size: 0.7vw;
    line-height: 1vw;
    color: #333;
}
.fixed-qr .more-qr,.fixed-qr .goto-top{
    background-position: center;
    background-repeat: no-repeat;
    background-size: 2.2vw;
    height:6vw;
    cursor: pointer;
}

.fixed-qr .more-qr{
    background-image: url(../../images/right_bar_qr.png);
}


.right-bar .goto-top{
    background-image: url(../../images/right_bar_back_top.png);
}

.right-bar.ant-back-top {
    width:auto;
    height:auto;
    position: static;
}

.fixed-qr .line{
    margin:0 auto;
    width: 4.2vw;
	height: 1px;
	background-color: #d7d7d7;
}